@import url(https://fonts.googleapis.com/css2?family=DM+Sans&family=Roboto&display=swap);
/* font-family: 'DM Sans', sans-serif; */
/* font-family: 'Roboto', sans-serif; */
:root {
  --main-black: #130a1d;
  --main-white: #ffffff;
  --main-blue: #2268a9;
  --main-red: #a92232;
  --main-violet: #79317e;
  --main-grey: #bebebe;
  --main-green: #22a927;
  --main-yellow: #ffb715;
  --main-max-width: 992px;
  --main-body-font: "DM Sans", sans-serif;
  /* --main-font: "Roboto", sans-serif; */
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #130a1d;
  background-color: var(--main-black);
  color: white;
  font-family: "DM Sans", sans-serif;
  font-family: var(--main-body-font);
  max-width: 992px;
  max-width: var(--main-max-width);
  min-height: 90vh;
  margin: 0 auto;
  padding: 30px 30px 0 30px;
}

.navbar {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  max-width: 992px;
  max-width: var(--main-max-width);
  margin: 5vh auto;
}

.navbar__a {
  color: white;
  text-decoration: none;
}

.anchor {
  color: white;
  text-decoration: none;
}

.footer {
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
}

.footer__a {
  color: #bebebe;
  color: var(--main-grey);
  text-decoration: none;
}

.footer__a:hover {
  color: #ffffff;
  color: var(--main-white);
}

.button {
  background-color: #2268a9;
  background-color: var(--main-blue);
  color: #ffffff;
  color: var(--main-white);
  width: 200px;
  height: 50px;
  border-radius: 30px;
  border: none;
  cursor: pointer;
  font-size: 1.5rem;
  text-transform: uppercase;
  transition: all 0.2s ease-in-out;
}

.title {
  font-size: 3rem;
}

/* @media (max-width: 768px) {
  body {
    padding: 0 30px;
  }
} */

.statsBar {
  height: 20px;
  min-width: 40%;
  background: none;
  border: 1px solid var(--main-white);
}

.statsBar__filler {
  height: 100%;
  transition: width 1s ease-in-out;
  background-color: var(--main-white);
  border-radius: "inherit";
  text-align: "right";
}

.statsBar__label {
  padding: 5px;
  color: white;
  font-weight: "bold";
}

.heroinfo {
  background-color: var(--main-black);
  position: fixed;
  top: 0px;
  right: 0px;
  width: 100%;
  max-width: 500px;
  height: 100vh;
  padding: 30px 30px 0 30px;
  overflow-y: auto;
}
.heroinfo__overlay {
  backdrop-filter: blur(5px) saturate(180%);
  -webkit-backdrop-filter: blur(16px) saturate(180%);
  background-color: rgba(39, 59, 95, 0.75);
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0px;
  right: 0px;
  z-index: 2;
}
.heroinfo__info-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 30px 0 60px 0;
  font-size: 1.5rem;
  min-height: 50%;
}
.heroinfo__button {
  display: block;
  margin: 0 auto;
  background-color: var(--main-red);
}

.heroinfo__button:hover {
  color: rgba(255, 255, 255, 1);
  box-shadow: 0 0px 60px var(--main-red);
}

.herofinder {
  /* background-color: tomato; */
  background-color: var(--main-black);
  position: fixed;
  top: 0px;
  right: 0px;
  width: 100%;
  max-width: 500px;
  height: 100vh;
  padding: 30px 30px 0 30px;
  overflow-y: auto;
}
.herofinder__overlay {
  backdrop-filter: blur(5px) saturate(180%);
  -webkit-backdrop-filter: blur(16px) saturate(180%);
  background-color: rgba(39, 59, 95, 0.75);
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0px;
  right: 0px;
  z-index: 2;
}
.herofinder__title {
  border-bottom: 2px solid var(--main-white);
}
.searchbar {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  color: black;
  margin: 20px 0;
}
.fa-search {
  font-size: 1.6rem;
  color: var(--main-white);
  transition: all 0.3s ease-in-out;
  cursor: pointer;
}
.fa-search:hover {
  color: var(--main-green);
}
.fa-times {
  font-size: 3rem;
  position: relative;
  left: 90%;
  cursor: pointer;
}
.fa-times:hover {
  color: var(--main-red);
  transition: all 0.3s ease-in-out;
}
.results__info-button {
  color: var(--main-white);
  font-size: 30px;
  justify-self: flex-end;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}
.searchbar__field {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--main-white);
  height: 30px;
  width: 80%;
  border-radius: 15px;
}
.searchbar__input {
  background: none;
  outline: none;
  margin-left: 6px;
  border: none;
  border-bottom: 1px solid black;
  width: 80%;
}
.results__item {
  display: grid;
  grid-template-columns: 100px 50px 1fr 3rem;
  align-items: center;
  justify-items: center;
  margin: 30px 0;
}
.results__img {
  height: 100px;
  max-width: 100px;
  overflow: hidden;
  border-radius: 60px;
  background-color: brown;
}
.results__name {
  font-size: 1.5rem;
  text-align: center;
}
.results__plus {
  font-size: 3rem;
  transition: all 0.3s ease-in-out;
  cursor: pointer;
}

.results__plus:hover {
  color: var(--main-green);
}
.results__info {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.results__p {
  font-size: 1.5rem;
}
.herofinder__error-msg {
  color: red;
  border-bottom: 2px solid red;
}
.herofinder__button {
  background: none;
  border: none;
}
.result-hero__img {
  width: 100px;
}
.herofinder__informative {
  display: flex;
  align-items: center;
  justify-content: space-around;
  color: var(--main-yellow);
  border: 2px solid var(--main-yellow);
  border-radius: 10px;
  padding: 5px;
}
.fa-exclamation-triangle {
  font-size: 2rem;
  margin-right: 10px;
}
.informative-error {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  color: red;
  margin: 20px auto;
  justify-content: center;
  border: 2px solid var(--main-red);
  padding: 10px;
}

.herofinder__added-heroes {
  display: flex;
  justify-content: space-between;
  font-size: 1.3rem;
  margin-top: 20px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 2px solid var(--main-white);
}
.header__span {
  background-color: var(--main-blue);
  padding: 2px 4px;
  border-radius: 5px;
}
.hero {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}
.card__info-button {
  color: var(--main-white);
  background: none;
  border: none;
  font-size: 35px;
  position: relative;
  left: 43%;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  z-index: 1;
}
.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: center; */
  border: 1px solid var(--main-white);
  border-radius: 5px;
  width: 300px;
  min-height: 550px;
  margin: 10px auto;
}
.hero__img {
  max-width: 100%;
  max-height: 100%;
}
.hero__portrait {
  width: 90%;
  position: relative;
  top: -20px;
}
.hero__name {
  font-size: 2rem;
}
.hero__stats {
  display: grid;
  grid-template-columns: 85px 0.7fr 70px;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
}
.team__stats {
  display: grid;
  grid-template-columns: 110px 0.9fr 55px;
  align-items: center;
  width: 100%;
  font-size: 1.2rem;
}
.card__plus {
  font-size: 5rem;
  margin: auto;
  transition: all 0.3s ease-in-out;
}
.card__plus:hover {
  color: var(--main-green);
}
.powerstats {
  margin: 20px 0;
}
.averages__title {
  font-size: 2rem;
}
.averages__info {
  display: flex;
  margin: 20px 0;
  max-width: 350px;
  justify-content: space-between;
  font-size: 1.3rem;
}

.main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  min-height: 80vh;
}

.main__section {
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: space-between;
}

.main__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 30px;
}

.main__img {
  width: 130px;
}

.main__text {
  max-width: 200px;
  text-align: center;
  margin-top: 20px;
}

.button--homepage {
  width: 15vw;
  min-width: 200px;
  margin-top: 5vh;
  margin-bottom: 15vh;
}

.button--homepage:hover {
  color: rgba(255, 255, 255, 1);
  box-shadow: 0 0px 60px #2268a9;
}

@media (max-width: 768px) {
  .main__section {
    display: flex;
    flex-direction: column;
  }
}

.back-button {
  width: 50px;
}

.wellcome {
  /* margin-left: 30px; */
  margin-top: 5vh;
}

.wellcome__title {
  font-size: 4rem;
}

.wellcome__text {
  color: var(--main-grey);
  font-size: 1.5rem;
}

.form {
  /* background-color: tomato; */
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 5vh;
  /* justify-content: space-around; */
}

.form__field {
  display: flex;
  justify-content: space-between;
  margin: 3vh 0;
  /* background-color: turquoise; */
  max-width: 500px;
  width: 100%;
}

.form__input {
  background: none;
  border: none;
  color: var(--main-white);
  font-size: 1.5rem;
  border-bottom: 2px solid var(--main-white);
  width: 90%;
}

.fa-user,
.fa-lock {
  font-size: 2rem;
}

.form__error-msg {
  color: red;
  border-bottom: 2px solid red;
}

